.rent-image-frame {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 700px;
  position: relative;
  overflow: hidden;
  z-index: -1;
  margin-top: -64px;

  .img-frame {
    width: 100%;
    height: 700px;
    object-fit: cover;
  }
}
@media only screen and (min-width: 600px) {
  .rent-image-frame {
    .img-frame {
      width: 100%;
      height: auto;
    }
  }
}
